.flex {
    display: flex;
}

/*<ProfileInfo />*/
.profileInfo {
    height: min-content;
}

/*<Status />*/
.status {
    height: min-content;
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
    background-color: #deb48c;
}

.active {
    height: min-content;
    width: 90%;
    padding: 20px;
    font-family: Calibri;
    font-size: 18px;
}

.notActive {
    height: min-content;
    padding: 5px 5px 5px 20px;
    border-radius: 6px;
    background-color: antiquewhite;
    font-family: Calibri;
    font-style: italic;
    font-size: 18px;
}

/*<Buttons />*/
.buttons {
    display: flex;
    padding: 10px;
}

.button {
    width: 75px;
    height: 49px;
    margin-right: 15px;
    border: 0px;
    border-radius: 10px;
    background-color: #deb48c;
    font-family: Arial;
}

.pbutton {
    width: 75px;
    height: 49px;
    border: 0px;
    border-radius: 10px;
    background-color: #deb48c;
    font-family: Arial;
}

/*<LinksArea />*/
.links {
    height: min-content;
    margin: 10px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 20px;
    background-color: #deb48c;
}

.linksArea {
    margin: 10px;
    padding-left: 5px;
    padding-right: 5px;
    border-radius: 5px;
    background-color: antiquewhite;
}

/*<UserPart />*/
.userPart {
    height: min-content;
    margin: 10px;
    border-radius: 20px;
    background-color: #e0f1fc;
}

.imageBody {
    display: flex;
}
.imageButtons {
    margin-left: 10px;
}
.image {
    width: 200px;
    height: 200px;
    border-radius: 8%;
}

.description {
    padding-top: 10px;
    padding-left: 16px;
}

.descriptionArea {
    margin: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 20px;
    padding-right: 20px;
    border-radius: 10px;
    background-color: antiquewhite;
}

.text {
    font-family: Calibri;
    font-style: italic;
    font-size: 18px;
    color: #5f3e10;
}